import React from 'react';
import { Typography } from '@material-ui/core';
import { useModal } from '../../hooks'
import Modal from './Modal'

export function Grid({ sectionTitle, labels, gridElements = [], gridCssClass = "" }) {
    const modalViewMore = useModal()

    const { misc: { buttonViewMore } } = labels

    return (
        <div className={"grid-container " + gridCssClass}>
            <GridModal open={modalViewMore.open} onClose={() => modalViewMore.onClose()} titulo={sectionTitle} isFullScreen={true} element={modalViewMore.element} labels={labels} />
            <Typography variant='h1' align="center" >{sectionTitle}</Typography>
            <div className="grid">
                {
                    gridElements.map((element, index) => (
                        <div className={"grid-element-container"} key={index} >
                            <div className='grid-element-img-container'>
                                <img src={element.img} alt="logo" />
                            </div>
                            <div className="grid-element-text-container">
                                <div className="top-text">
                                    <Typography variant='h3' align='center' >{element.titulo.toUpperCase()}</Typography>
                                    <Typography variant='h6' align='center' >{element.subtitulo.toUpperCase()}</Typography>
                                </div>
                                <button onClick={(event) => modalViewMore.onOpen(element)}><Typography variant='body1' align='center' >{buttonViewMore.toUpperCase()}</Typography></button>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    );
}

function GridModal({ open, onClose, titulo, isFullScreen, element, labels }) {
    const { misc: { linkLabels, linkIcons } } = labels
    const { img = "", titulo: elementTitle = "", subtitulo = "", descripcion = "", hashtags = [], links = [] } = element || {}
    return (
        <Modal open={open} onClose={onClose} titulo={titulo} isFullScreen={isFullScreen}>
            <div className="grid-element-modal-content-container scrollable">
                <div className='grid-element-modal-img-container'>
                    <img src={img} alt="logo" />
                </div>
                <div className="grid-element-modal-text-container">
                    <Typography variant='h2'  >{elementTitle.toUpperCase()}</Typography>
                    <Typography variant='h4'  >{subtitulo.toUpperCase()}</Typography>
                    <Typography variant='body1'  >{hashtags.join(', ').toUpperCase()}</Typography>
                    {descripcion !== "" && <Typography variant='body1'  >{descripcion}</Typography>}
                </div>
                <div className="grid-element-modal-links-container">
                    {
                        links.map((linkElement, index) => {
                            const { link = "", icon = null, type = "" } = linkElement
                            return <div className="link" key={'lnik-icon' + index}>
                                <a href={link} target="_blank" rel="noopener noreferrer" >{linkIcons[type] || ""} <Typography variant='body1'  >{linkLabels[type] || ""}</Typography></a>
                            </div>
                        })
                    }
                </div>
            </div>
        </Modal>
    )

}