import React from 'react';
import { Typography } from '@material-ui/core';

export function Topbar({ language, setLanguage }) {
    return (
        <div className="topbar">
            <Typography variant='body1' >LEANDRO ERCOLI</Typography>
            <div className="topbar-lang">
                <Typography variant='body1' className={language === "es" ? "lang-active" : ""} onClick={() => setLanguage("es")}>ES.</Typography>
                <Typography variant='body1' className={language === "en" ? "lang-active" : ""} onClick={() => setLanguage("en")} >EN.</Typography>
            </div>
        </div>
    )
}